}

canvas {
  position:absolute;
  top:0px;
  left:0;
  right: 100;
  width:100%;
  height:100%;
  z-index: -1;
  }
h1{

  display: flex;
  justify-content: right;
  padding: 130px;
  font-size: 4rem;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


